import React, { useState } from 'react';
import './Navbar.css';

function Navbar() {
  const [navbar,setNavbar] = useState(false);
  const onScroll=()=>{
    if(window.scrollY>80){
      setNavbar(true);
    }else{
      setNavbar(false);
    }
  }
  window.addEventListener('scroll',onScroll);
  return (
    <div className={navbar?"Nav-bar active":'Nav-bar'}>
      <div className={navbar?'container-nav active':'container-nav'}>
      <div className='logo'>
        <img src="data:image/webp;base64," alt="" title="" data-pagespeed-url-hash="1554750824" onload="pagespeed.CriticalImages.checkImageForCriticality(this);"/>
      </div>
        <ul className="nav-ul">
          <li className="nav-item active">
            <a className="element" href="#">HOME</a>
          </li>
          <li className="nav-item">
            <a className="element" href="#">ABOUT US</a>
          </li>
          <li className="nav-item">
            <a className="element" href="#">ONLINE/OFFLINE</a>
          </li>
          <li className="nav-item">
            <a className="element" href="#">OUR SERVICES</a>
          </li>
          <li className="nav-item">
            <a className="element" href="#">PROGRAMS</a>
          </li>
        </ul>
        </div>
    </div>
  );
}


export default Navbar;

